.App {
  text-align: center;
  font-family: "Lato", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

body {
  margin: 0;
}

.Menu,
.Quiz {
  width: 700px;
  height: 550px;
  padding: 25px;
  border-radius: 10px;
  background-color: rgba(82, 136, 150, 0.85);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50px;
}

.Menu {
  color: white;
}

.menu-text {
  width: 90%;
  text-align: justify;
  font-size: 18px;
}

.Quiz h1 {
  font-size: 16px;
  width: 90%;
  font-weight: 400;
  color: white;
  line-height: 20px;
  text-align: left;
}

.btn-start,
.btn-again {
  width: 300px;
  height: 50px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  font-size: 20px;
  cursor: pointer;
}

.btn-start:hover,
.btn-again:hover {
  cursor: pointer;
  background-color: RGB(82, 136, 150);
  border: 2px solid white;
  color: white;
}

.options button {
  width: 90%;
  font-size: 16px;
  font-family: "Lato", sans serif;
  text-align: left;
  padding-left: 15px;
  height: 80px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.options button:hover {
  background-color: lightgrey;
}

.options button:focus {
  background-color: lightsalmon;
}

.Quiz p {
  position: absolute;
  top: 15px;
  left: 57px;
  font-weight: 700;
  font-size: 14px;
}
.EndScreen {
  width: 700px;
  height: 550px;
  border-radius: 10px;
  background-color: rgba(82, 136, 150, 0.85);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50px;
}

.btn-next,
.btn-finish {
  position: absolute;
  bottom: 20px;
  right: 60px;
  font-size: 10px;
  padding: 5px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
}

.btn-next:hover,
.btn-finish:hover {
  background-color: rgb(82, 136, 150);
  border: 2px solid white;
  color: white;
}

.sliderimg {
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.progress-bar-rect-wrap-container {
  height: 20px;
  margin-bottom: 15px;
}

.Stakeholders {
  text-align: left;
  font-weight: bold;
}

.Performance {
  width: 700px;
  height: 550px;
  border-radius: 10px;
  background-color: rgba(82, 136, 150, 0.85);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  position: absolute;
  top: 50px;
  padding-top: 0;
}

.Performance h2 {
  margin-top: 0;
}

.Performance p {
  margin-top: 0;
}
